<template>
    <div class="my">
        <div class="my-Info" :style="{background: isLogin ? 'url(' + userInfo.userBg + ') no-repeat center/cover' : '#ccc' }">
            <div class="replace" >
                <van-uploader :after-read="afterRead" v-if="isLogin">更换背景</van-uploader>

                <span class="login" v-else @click="$router.push({ name: 'Login' })">登录</span>
            </div>

            <div class="userInfo">
                <div class="user-img">
                    <van-uploader :after-read="portrait">
                        <img class="auto-img" :src="isLogin ? userInfo.userImg : photo" alt="">
                    </van-uploader>
                </div>

                <div class="user-name">
                    <div class="name">{{ isLogin ? userInfo.nickName : '登录账号体验更多功能' }}</div>
                    <div class="label">{{ userInfo.desc == "" ? "这家伙什么都没有留下" : userInfo.desc }}</div>
                </div>
            </div>
        </div>

        <van-cell-group>
            <van-cell :title="item.title" is-link v-for="(item, index) in list" :key="index" @click="$router.push(item.name)" />
        </van-cell-group>

        <NavBar></NavBar>
    </div>
</template>

<script>
    import { Toast } from "vant";
    // 导入导航组件
    import NavBar from "@/components/NavBar";

    export default {
        name: "My",

        data() {
            return {
                list: [
                    { title: "账号管理", name: "Management" },
                    { title: "地址管理", name: "Address" },
                    { title: "我的收藏", name: "MyCollect" },
                    { title: "安全中心", name: "Safety" },
                ],

                // 用户信息
                userInfo: {},
                // 是否登录
                isLogin: false,
                // 默认头像
                photo: require("../assets/images/photo.png"),
            }
        },

        methods: {

            // 获取用户信息
            getUserInfo() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findMy",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code = "A001") {
                        this.userInfo = res.data.result[0];

                        this.isLogin = true;
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();

                    this.isLogin = false;

                });
            },

            // 更换背景
            afterRead(file) {
                
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                let fileImg = file.content.split(",");

                let imgType = ["jpg", "jpeg", "png", "gif"]

                this.axios({
                    method: "POST",
                    url: "/updateUserBg",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        ...imgType,
                        serverBase64Img: fileImg[1],
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == "I001") {

                        this.userInfo.userBg = res.data.userBg;

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 上传头像
            portrait(file) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                let fileImg = file.content.split(",");

                let imgType = ["jpg", "jpeg", "png", "gif"]

                this.axios({
                    method: "POST",
                    url: "/updateAvatar",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        ...imgType,
                        serverBase64Img: fileImg[1],
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == "H001") {

                        this.userInfo.userImg = res.data.userImg;

                    }else{

                        Toast(res.data.msg);
                        
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

        },

        components: {
            NavBar,
        },

        created() {

            // 获取用户信息
            this.getUserInfo();

            this.$store.state.active = 3;

        },
    }
</script>

<style lang="less" scoped>
    .my {
        height: calc(100vh - 45px);
    }

    .my-Info{
        height: 200px;
        padding: 20px;
        color: #f0f0f0;
        background-color: #ccc;
        font-size: 14px;
        box-sizing: border-box;

        .replace{
            display: flex;
            justify-content: flex-end;

            .login{
                color: #1989FA;
            }
        }

        .userInfo{
            display: flex;
            // position: absolute;
            // top: 50%;
            // left: 20px;
            margin-top: 20px;
            // transform: translateY(-50%);

            .user-img{
                width: 80px;
                height: 80px;
                // background-color: #ccc;
                border-radius: 50%;
                border: 4px solid rgba(255, 255, 255, 0.3);
                // box-shadow: 0 0 5px 2px #1989FA;
                box-sizing: border-box;
                overflow: hidden;
            }

            .user-name{
                display: flex;
                width: 150px;
                margin-left: 20px;
                flex-direction: column;
                justify-content: space-between;

                .name{
                    font-size: 16px;
                    line-height: 30px;
                }

                .label{
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }

        }
    }
</style>